import { ConnectorRuntime } from '@transai/connector-runtime';
import { ApiResultHandler } from './api-result.handler';
import { ApiExtractorService } from './api-extractor/api-extractor.service';
import { ApiSourceConfig, isApiSourceConfigType } from './types';
import { KafkaService } from './kafka/kafka.service';
import { TokenManager } from './token-manager/token-manager';

export class ConnectorRunnerApiSource extends ConnectorRuntime<ApiSourceConfig> {
  readonly CONNECTOR_INSTANCE: string = 'XOD_CONNECTOR_API_SOURCE_CONFIG';

  private kafkaWrapper: KafkaService | undefined = undefined;

  #apiExtractorServices: Array<ApiExtractorService> = [];

  override init = async (): Promise<void> => {
    const store = this.offsetStoreInstance;
    if (!store) {
      throw new Error(
        'Offset store is not defined. Please provide an temp location for the offset store.',
      );
    }

    const config = this.config!;

    this.kafkaWrapper = new KafkaService(this.kafkaService!);

    const apiResultHandler = new ApiResultHandler(
      config,
      this.kafkaWrapper,
      store,
    );

    let tokenManager: TokenManager | undefined;

    if (config.tokenUrl && config.clientId && config.clientSecret) {
      tokenManager = new TokenManager(
        config.tokenUrl,
        config.clientId,
        config.clientSecret,
      );
    }

    this.#apiExtractorServices = config.apiCalls.map(
      (apiConfig) =>
        new ApiExtractorService(
          config,
          apiConfig,
          apiResultHandler,
          store,
          tokenManager,
        ),
    );
  };

  override exit = async () => {
    this.#apiExtractorServices.forEach((service) => service.stop());
    this.#apiExtractorServices = [];
  };
}
